<template>
  <section
    :id="sectionId"
    ref="section"
    :class="sectionClasses"
  >
    <div class="container">
      <slot />
    </div>
  </section>
</template>

<script>
import { imageMixin, sectionMixin } from './mixins'

export default {
  // Represents a section of 
  // the Hero page

  name: 'BaseSection',
  mixins: [imageMixin, sectionMixin],
  props: {
    // color: {
    //   type: String,
    //   default: null
    // },
    // iconColor: {
    //   type: String,
    //   default: 'text-dark'
    // },
    // sectionId: {
    //   type: String,
    //   required: false
    // },
    // src: String,
    // theme: {
    //   type: String,
    //   default: 'light'
    // },
    // textWhite: Boolean
  },

  computed: {
    sectionClasses() {
      return [
        {
          'bg-image': this.hasImage,
          [`bg-${this.theme}`]: this.theme,
          'text-white': this.textWhite,
          'shadow-sm': true
        },
        this.color
      ]
    },

    hasImage() {
      return this.src != null
    }
  },

  mounted() {
    if (this.hasImage) {
      this.$refs.section.style.backgroundImage = this.getBackgroundUrl(this.src)
    }
  }
}
</script>

<style scoped>
  section {
    position: relative;
    text-align: center;
  }

  @media(min-width: 993px) {
    section {
      padding: 90px 45px 100px;
    }
  }
</style>
